import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { AlertController, ModalController } from '@ionic/angular';

@Component({
  selector: 'app-listas',
  templateUrl: './listas.component.html',
  styleUrls: ['./listas.component.scss'],

})
export class ListasComponent implements OnInit {

  @Input("strings")
  strings: string[] = []

  @Input("titulo")
  titulo: string

  @Input("saveButton")
  saveButton: boolean = false;

  @Input("closeButton")
  closeButton: boolean = false;

  @Output("deleted")
  deleted: EventEmitter<any> = new EventEmitter();

  @Input("isModal")
  isModal: boolean = true;




  constructor(public alertController: AlertController, public modalCtrl: ModalController) { }

  ngOnInit() {

  }

  addString() {
    this.presentAlertPrompt()
  }

  remov(stringRemovida) {
    this.strings.forEach((element, index, arr) => {
      if (element == stringRemovida) {
        arr.splice(index, 1)
      }
    });
    this.deleted.emit(stringRemovida);
  }

  async presentAlertPrompt() {
    const alert = await this.alertController.create({
      header: 'Adicionar',
      inputs: [
        {
          name: 'palavra',
          type: 'text',
          placeholder: 'Descrição da ' + this.titulo
        }
      ],
      buttons: [
        {
          text: 'Cancelar',
          role: 'cancel',
          cssClass: 'secondary',
          handler: () => {
          }
        }, {
          text: 'Salvar',
          handler: data => {
            let a = []
            a.push()
            let t = this.strings.push(data.palavra);
          }
        }
      ]
    });

    await alert.present();
  }

}
